* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 100vh;
}

.resgister-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgb(223, 207, 190);
  background-image: url('img3.jpg') ;
  background-size: 100% 100%;
  
}

.login-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgb(223, 207, 190);
}

/* transection page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

.txt{
  color: black;
  font-size: x-large; 
  text-decoration: none;
}


/* antd icons */

.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
    font-size: 20;
    cursor: pointer;

}
.active-icon{
  color:black;
}

.inactive-icon{
  color:gray;
}

.btn1:hover {
  background-color: red;
}

.reg{
  background-color: rgb(80, 249, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  border-width: 0.1rem;
  box-shadow: 10px 10px 10px ;
}

.btm{
  background-color: orange;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 9vh;
}

.nam{
  font-size: 100px;
}

.box {
  width: 600px;
  border: 5px solid brown;
  background-color:burlywood;
  padding: 50px;
  margin: 20px;
}
